	module.exports = {"data":{"markdownRemark":{"html":"<p>This episode, I’ve got a real treat: a cutting edge tech project from my friend, David Fusco! David’s interested in emerging technologies like the voice controlled tech in Amazon’s Echo or Google’s Home devices. He’s created a voice activated quiz using Amazon’s Alexa technology and is generating xAPI statements from it. I wanted to talk to him about the whys and hows of working on a cutting-edge project like this.</p>\n<iframe src=\"https://simplecast.com/e/59981?style=medium-light\" width=\"100%\" height=\"94px\" frameborder=\"0\" scrolling=\"no\" seamless=\"\"></iframe>\n<p>Be sure to reach out to Dave on Twitter. He’s <a href=\"https://twitter.com/DrDaveFusco\">@DrDaveFusco</a> And as always, you can check out the shownotes here: <a href=\"http://dearinstructionaldesigner.simplecast.fm/\">http://dearinstructionaldesigner.simplecast.fm/</a></p>","frontmatter":{"date":"February 23, 2017","path":"/blog/dearid-episode-38-building-a-quiz-with-amazon-alexa-and-dr-dave-fusco","title":"#DearID Episode 38: Building a Quiz with Amazon Alexa and Dr. Dave Fusco","tags":["amazon alexa","quiz","voice interface","xAPI"],"categories":["Coding","Dear ID Show"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":3,"src":"/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c6512.jpg","srcSet":"/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c7d05.jpg 203w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-784f5.jpg 405w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c6512.jpg 810w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-adada.jpg 1215w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-1c372.jpg 1500w","sizes":"(max-width: 810px) 100vw, 810px"}}}}}},"pathContext":{}}

